* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html {
  min-height: 1vh; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
  min-height: 1vh; }

/* COLORS */
/* PADDINGS AND MARGINS */
/* MEDIA QUERIES */
@media (min-width: 768px) {
  html {
    font-size: 46.875%; } }

@media (min-width: 1024px) {
  html {
    font-size: 62.5%; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 0;
  margin: 0;
  height: 100vh;
  width: 100vw;
  z-index: 10; }

.modal {
  background-color: #e7e8ec;
  border-radius: 4px;
  height: 372px;
  margin-top: 8vh;
  z-index: 30;
  padding: 0; }

.heading {
  height: 72px;
  padding: 24px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  background-color: #ffffff;
  font-size: 18px;
  color: #585b60; }

.heading img {
  height: 16px;
  width: 16px;
  float: right;
  margin: 4px; }

form {
  background-color: #e7e8ec;
  margin: 0px;
  padding: 24px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px; }

form label {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  color: #a0a1a5;
  line-height: 18px; }

form .text-input {
  border: 1px solid #c3cbd3;
  border-radius: 4px;
  height: 36px;
  width: 100%;
  margin-bottom: 18px; }

form .button-wrap {
  padding-top: 36px;
  text-align: right; }

form .button-wrap button {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  padding: 12px;
  width: 94px;
  margin: 0 0 0 12px; }

form .button-wrap .cancel {
  background-color: #585b60; }

form .button-wrap .save {
  background-color: #cb9c59; }



